/* ----------- TABLE ---------------- */
.OrdersCustomer-wrapper-table {
    margin-bottom: 10px;
    overflow: auto;
}

.OrdersCustomer-table {
    border: 1px solid gainsboro;
    border-radius: 5px;
    border-spacing: 0;
}

.OrdersCustomer-table tr {
    border-radius: 4px;
    box-shadow: 0 0 2px silver;
}

.OrdersCustomer-table tbody tr.active {
    background-color: silver;
}

.OrdersCustomer-table tbody tr:hover {
    cursor: pointer;
}

.OrdersCustomer-table input {
    border: 1px solid gainsboro;    
}

.OrdersCustomer-state {
    background-color: rgb(238, 238, 238);
    display: block;
    width: 100px;
    text-align: center;
    border-radius: 3px;
}

.OrdersCustomer-table td {
    padding: 5px;    
}

.OrdersCustomer-col-number {
    text-align: right;
}

.OrdersCustomer-col-text {
    text-align: left;
}


/* --------- CARD DETAIL ----------- */

.OrdersCustomer-detail {
    width: 310px;    
}

/* ----------- TABLE DETAIL --------------- */
.OrdersCustomer-table-items {
    border-spacing: 0;    
}

.OrdersCustomer-table-items td {
    padding: 3px;
    vertical-align: text-top;      
}

.OrdersCustomer-table-items tfoot td{
    border-top: 1px solid gainsboro;
}