.Cart-title {
    /* border: 1px solid red; */
    line-height: var(--products-title-height);
    text-transform: uppercase;
    text-align: center;
    font-size: 1.5rem;
    box-shadow: 0 0 3px gainsboro;
}

/* ------------- TABLE --------------- */

.Cart-table-wrapper {
    /* background-color: black; */
    /* padding: 5px; */
    margin-bottom: 10px;
    overflow: auto;
}

.Cart-table {
    border: 1px solid gainsboro;
    /* background-color: grey; */
    /* display: block; */
    width: 550px;
    padding: 5px;
    border-radius: 3px;    
}

.Cart-row {
    /* border: 1px solid green; */
    /* background-color: green; */
    max-height: 100px;
    padding: 5px;    
}

.Cart-row.no-title {
    /* border: 1px solid blue; */
    margin-bottom: 10px;
    box-shadow: 0 0 3px silver;
    border-radius: 3px;
}

.Cart-col-product {
    /* border: 1px solid fuchsia; */
    width: 300px;
    display: flex;
    align-items: center;
}

.Cart-container-img {
    /* border: 1px solid green; */
    width: 100px;
    height: 100px;
}

.Cart-container-img img {
    width: 100px;
    height: 100px;
    object-fit: cover;
}

.Cart-description {
    /* border: 1px solid yellow; */
    margin-left: 10px;
}

.Cart-col-qty {
    /* border: 1px solid red; */
    width: 70px;
}

.Cart-qty {
    border: 1px solid gainsboro;
    width: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Cart-col-price {
    /* border: 1px solid red; */
    width: 80px;
    text-align: right;
}

.Cart-col-amount {
    /* border: 1px solid red; */
    text-align: right;
    width: 80px;    
}

.Cart-icon {
    font-size: 1.5rem;
    color: rgb(48, 48, 48);    
    cursor: pointer;
}

.Cart-col-icon {
    width: 20px;
}

/* ------------- SUMMARY ------------- */

.Cart-summary {
    /* border: 1px solid black; */
    width: 320px;   
}

.Cart-btn-order {
    background-color: black;
    color: white;
    width: 100%;
    margin-top: 20px;
    padding: 10px;
    border-radius: 5px;
}

.Cart-btn-order:hover {
    cursor: pointer;
}

.Cart-clean-cart {
    color: blue;
    text-decoration-line: underline;
}

.Cart-clean-cart:hover {
    cursor: pointer;
}