.Detail-top {
    box-shadow: 0 0 3px gainsboro;
}

.Detail-icon {
    font-size: 1.5rem;
    margin: 10px;
}

.Detail-icon:hover,
.Detail-btn-cart {
    cursor: pointer;
}

.Detail-wrapper {
    box-shadow: 0 0 3px gainsboro;
    padding-top: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-start;
    padding-bottom: 20px;
}

.Detail-card-image,
.Detail-card-data {
    width: 300px;
    height: 400px;
    margin: 10px 10px;
}

.Detail-card-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* minus padding izq-right */
.Card {
    padding: 20px 10px;
}

.Detail-card-data {    
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.Detail-card-data-text {
    /* background-color: grey; */
    height: 100%;
}

.Detail-title,
.Detail-price {
    font-size: 1rem;
    margin-top: 5px;
    font-weight: 600;
}

.Detail-price {
    color: grey;
}

.Detail-description {
    font-size: .75rem;
}

.Detail-qty {
    border: 1px solid silver;
    border-radius: 5px;
    display: inline;
    width: 110px;
    margin-top: 10px;
    line-height: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;    
}

.Detail-stock {
    border: 1px solid silver;
    border-radius: 5px;
    margin-top: 10px;
    line-height: 2rem;
    text-align: center;
    color: grey;
}

.Detail-card-data-buttons {
    /* background-color: gainsboro; */
    height: 145px;
}

.Detail-btn-cart {
    background-color: black;
    color: white;
    display: block;
    width: 100%;
    line-height: 3rem;    
    border-radius: 5px;
}

@media only screen and (max-width: 500px) {
    .Detail-wrapper {
        flex-direction: column;
        align-items: center;
    }

    .Detail-card {
        width: 95%;
        margin-bottom: 10px;
    }
}