/* ----------- TABLE ------------ */
.CustomersList-wrapper-table {
    overflow-x: auto;
}

.CustomersList-table {
    width: 500px;
    margin-bottom: 10px;
}

.CustomersList-icon-delete {
    color: rgb(199, 0, 0);
    font-size: 2rem;
}


/* ----------- CARD --------------- */
.Card-customer-border {
    /* border: 1px solid red; */
